import React from 'react';
import style from './Field.scss';
var Field = function (props) {
    var label = props.label, value = props.value;
    return (React.createElement("div", { className: style.field },
        React.createElement("div", { className: style.label }, label),
        React.createElement("div", { className: style.value }, value)));
};
export default Field;
