import React from 'react';
import { DashboardOutlined, TableOutlined } from '@ant-design/icons';
import { Navigate } from 'react-router-dom';
import Login from '@/pages/login';
import Layout from '../layout';
import Dashboard from '@/pages/dashboard';
import NotFoundPage from '@/pages/404';
import TableList from '@/pages/TableList';
import List from '@/pages/List';
var routes = [
    {
        path: '/login',
        component: React.createElement(Login, null),
        hidden: true,
    },
    {
        path: '/',
        component: React.createElement(Layout, null),
        hidden: true,
        children: [
            {
                path: '',
                index: true,
                component: React.createElement(Navigate, { to: "/dashboard", replace: true }),
            },
            {
                path: '/dashboard',
                name: 'dashboard',
                component: React.createElement(Dashboard, null),
                icon: React.createElement(DashboardOutlined, null),
            },
            {
                path: '/list',
                name: '列表页',
                component: React.createElement(List, null),
                icon: React.createElement(TableOutlined, null),
                children: [
                    {
                        path: 'table-list',
                        name: '查询表格',
                        component: React.createElement(TableList, null),
                    },
                    {
                        path: 'tree',
                        name: 'Tree',
                        component: React.createElement(Dashboard, null),
                    },
                ],
            },
            {
                path: '*',
                component: React.createElement(NotFoundPage, null),
                hidden: true,
            },
        ],
    },
];
export default routes;
